body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.navMenu.hide {
width: 0;
border: 0 !important;

}

.navMenu.display {
width: 20%;
overflow: hidden;

}

#selHomeNav, #selAboutNav, #selProjectsNav, #selContactNav {
  font-weight: bolder; 
  color: whitesmoke;

}

#navMenuHome {
  background: -webkit-linear-gradient(135deg, whitesmoke, #000000);
  background: linear-gradient(-45deg, whitesmoke, #000000);
  border: 1px solid black;

}


#navMenuAbout {
  background: -webkit-linear-gradient(135deg, #9e0012, #000000);
  background: linear-gradient(-45deg, #9e0012, #000000);
  border: 1px solid #9e0012;

}

#navMenuProjects {
  background: -webkit-linear-gradient(135deg, #2d8e39, #000000);
  background: linear-gradient(-45deg, #2d8e39, #000000);
  border: 1px solid #2d8e39;

}

#navMenuContact {
  background: -webkit-linear-gradient(135deg, #00329e, #000000);
  background: linear-gradient(-45deg, #00329e, #000000);
  border: 1px solid #00329e;

}

.navMenu {
height: 100%;
position: fixed;
top: 0;
left: 0;
-webkit-transition: 0.5s;
transition: 0.5s;
overflow: scroll;
z-index: 10;
font-family: 'Bangers', cursive !important;
font-weight: lighter; 

}

.navMenu p {
padding: 8px 8px 8px 32px;
text-decoration: none;
color: lightgray;
display: block;
-webkit-transition: 0.3s;
transition: 0.3s;
}

.navMenu p:hover {
font-weight: bolder;
color: whitesmoke;
}

.navMenuSelect {
font-size: 30px;
}

.menuItems {
margin-top: 30%;
}

.navClose {
position: absolute;
top: 2%;
right: 5%;
font-size: 25px;
margin-left: 50px;
}

@media only screen and (min-device-width: 0px) and (max-device-width: 480px) {

  .navMenu.display {
  width: 60%;
  overflow: hidden;
  }
  .navMenuSelect {
  font-size: 20px;
  }

  .navClose {
  position: absolute;
  top: 1.5%;
  right: 8%;
  font-size: 15px;
  margin-left: 50px;
  }

}


@media only screen and (min-device-width: 480px) and (max-device-width: 1050px) {

  .navMenu.display {
  width: 30%;
  overflow: hidden;
  }
}
